.story{overflow:scroll;height:100%}.story .item{padding:10px}.story .item.text p{text-align:justify;margin:0px}.story .item .createdAt{margin:0;font-style:italic;text-align:center;opacity:0.4}.story .item.player{background-color:#E0E2E2;border-radius:5px;margin:10px}.story .item.attachments{margin:10px;border-radius:5px;background:#E0E2E2}.story .item.attachments .no-attachments{text-align:center}.story .project-name{display:inline;font-weight:bold}.story .story-color-icon{height:18px;width:18px;border-radius:50%;float:right;margin-top:-8px;padding:4px}.story .story-color-icon>div{height:100%;width:100%;border-radius:50%}.story .image{width:100%;text-align:center}.story .image img{max-height:400px;max-width:100%;border-radius:5px}.story:after{content:' ';display:block;margin-bottom:3em}
