.slide-container{position:absolute;bottom:0;left:0;width:100%;pointer-events:auto;transition:height 500ms ease}.slide-container.fullscreen{height:100%}.slide-container.fullscreen.closed{height:0%}.slide-container.halfscreen{height:50%}.slide-container.halfscreen.closed{height:0%}.slide-container .slide-inner-container{padding-top:45px;background-color:#ffffff;height:100%}.slide-container .slide-close-button{position:absolute;top:0;left:0px;text-align:center;width:100%;transition:top 500ms ease, left 500ms ease, opacity 500ms ease;left:0px;top:0px;opacity:1}.slide-container .slide-close-button button{background-color:transparent;width:42px;height:42px;margin:3px;padding:4px;border-radius:50%;transition:background-color 500ms ease}.slide-container .slide-close-button button img{width:100%;height:100%}.slide-container .slide-close-button.detached{top:-25px;left:8px}.slide-container .slide-close-button.detached button{background-color:#F57D8D}.slide-container .slide-close-button.detached button img{filter:invert(1)}.slide-container .slide-close-button.closed{opacity:0}.slide-container .drag-handle{position:absolute;top:0;left:100px;right:100px}.slide-container .drag-handle>div{margin:8px auto;background:#F57D8D;height:5px;width:50px;border-radius:2px;opacity:0.2}
