.records-list{display:flex;align-items:center;justify-content:flex-start;flex-direction:column;padding:10px;overflow:scroll;height:100%}.records-list:after{content:' ';display:block;margin-bottom:3em}.records-list.center{justify-content:center}.records-list .item{background-color:#E0E2E2;text-align:left;width:100%;margin:10px 0px;border-radius:5px}.records-list .item .item-content{margin:10px}.records-list .item .author{font-style:italic}.records-list .item.uploaded{opacity:0.5}.records-list .center-item{justify-self:center;text-align:center;color:#666;width:100%}.records-list .center-item .input-element{width:80%;margin:0 auto}.records-list .line{width:100%;border-top:1px solid #000;opacity:0.1}.record{display:flex;align-items:center;justify-content:flex-start;flex-direction:column;padding:10px;overflow:scroll;height:100%}.record:after{content:' ';display:block;margin-bottom:3em}.record.center{justify-content:center}.record .item{text-align:left;width:100%;margin:5px 0px}.record .item .createdAt{margin:0;font-style:italic;text-align:center;opacity:0.4}.record .item.location{height:200px}.record .item.camera{background-color:#E0E2E2;border-radius:5px}.record .item.recorder{background-color:#E0E2E2;border-radius:5px}.record .item.recorder .recorder{width:auto;padding:5px}.record .item.saved p{margin:0;font-style:italic;text-align:center;opacity:0.4}
