.input-element{text-align:center;padding:5px;margin:0px;background-color:#E0E2E2;border-radius:5px;font-size:1rem}.input-element.bright{background-color:#ffffff}.input-element input,.input-element textarea{font-family:'Roboto', sans-serif;width:100%;display:block;border:none;margin:0;padding:0;background-color:none;font-size:1.1em;resize:none;box-shadow:none;font-style:italic;background-color:transparent;color:#286FFF}.input-element input::placeholder,.input-element textarea::placeholder{opacity:0.4;color:#286FFF}.input-element textarea:focus,.input-element input:focus{outline:none}.input-element .input-text-with-button{display:flex;align-items:stretch}.input-element .input-text-with-button input{float:left;padding:4px}.input-element .input-text-with-button .icon{background:none;width:32px;height:32px;padding:4px;margin:0}.input-element .input-text-with-button .icon img{width:100%;height:100%}.input-element .input-text-with-button .icon:disabled{opacity:0.2}.button-group{text-align:center;width:100%;margin:0 auto}.button-group button{display:inline-block;margin:10px 5px;width:auto}.text-input{padding:0px}.button-delete{transition:background-color 0.6s ease}.button-delete.confirm{background-color:yellow;color:black}.author-input{display:flex;align-items:center;justify-content:flex-start;flex-direction:column;height:100%;justify-content:center;padding:0 10px}.author-input .center-item{justify-content:center;justify-self:center;text-align:center;color:#666}
