.project-select{display:flex;align-items:center;justify-content:flex-start;flex-direction:column;margin:10px;justify-content:center;height:100%;align-items:stretch;padding:0 10%}.project-select>div{pointer-events:auto}.project-select a{display:inline-block;width:100%;height:100%}.project-select .project-select-last{margin-top:10px;padding:10px;border-radius:5px;display:block;text-align:center;background:#F57D8D}.project-select .project-select-last a{color:#286FFF;font-weight:bold}.project-select .error{color:white;background-color:red;font-weight:bold;opacity:0.4;font-size:0.9rem;margin:10px 0px;padding:5px;text-align:center;height:3em;border-radius:5px;visibility:visible}.project-select .error:empty{visibility:hidden}.project-info{padding:10px;overflow:scroll;height:100%}.project-info p{text-align:justify}.project-info .story-list{width:100%}.project-info .story-list:after{content:' ';display:block;margin-bottom:3em}.project-info .story-list ul{list-style-type:none;padding:0}.project-info .story-list li{background-color:#E0E2E2;padding:10px;margin:10px 0px;border-radius:5px}.project-info .story-list h4{display:inline}.project-info .story-list .date{float:right}.project-info .story-list .empty{padding:10px 0;text-align:center;width:100%}.project{display:flex;align-items:center;justify-content:flex-start;flex-direction:column;margin:10px}.project>div{pointer-events:auto}.project.center{justify-content:center;height:100%}.project .details{background-color:#eeeeee;width:100%;text-align:left;box-shadow:6px 6px black}.project .details .item-content{margin:10px}.project .details:active{box-shadow:4px 4px gray}.project .change-button{margin:10px 0px}.project .center-item{text-align:center;color:#666;width:100%;max-width:80%}.project .center-item .input-element{width:80%;margin:0 auto}
