.location_picker{position:relative;height:100%;width:100%}.location_picker .mapboxgl-map{border-radius:5px}.location_picker .marker{transform:translate(-50%, -50%);height:14px;width:14px;border-radius:50%;padding:5px;background:rgba(245,125,141,0.4)}.location_picker .marker .innerCircle{height:100%;width:100%;border-radius:50%;background:#F57D8D}.location_picker .crosshair{position:absolute;top:50%;left:50%;width:50px;height:50px;margin:-25px 0px 0px -25px;z-index:999;pointer-events:none}.location_picker .button-group{position:absolute;bottom:0px;width:100%;z-index:998}
